/* CategoryDisplay.css */

:root {
    --background-color: white;
    --category-title-color: #333;
    --card-background: #fff;
    --border-radius: 8px;
    --card-shadow: rgba(0, 0, 0, 0.1) 0px 4px 12px;
    --primary-color: #007185;
  }
  
  .category-display {
    padding: 20px;
    background-color: var(--background-color);
    display: flex;
    flex-direction: column;
    gap: 20px;
  }
  
  /* Row layout for two categories per row */
  .category-row {
    display: flex;
    justify-content: space-between;
    gap: 20px;
  }
  
  /* Each category container */
  .category-container {
    width: 48%;
    background-color: var(--card-background);
    border-radius: var(--border-radius);
    box-shadow: var(--card-shadow);
    padding: 16px;
  }
  
  .category-title {
    font-size: 1.5em;
    color: var(--category-title-color);
    margin-bottom: 12px;
    font-weight: 600;
  }
  
  /* Grid for product cards within each category */
  .product-grid {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 10px;
  }
  
  /* Product card styling */
  .product-card {
    background-color: var(--card-background);
    border-radius: var(--border-radius);
    box-shadow: var(--card-shadow);
    padding: 10px;
    cursor: pointer;
    text-align: center;
    transition: transform 0.2s, box-shadow 0.2s;
  }
  
  .product-card:hover {
    transform: scale(1.05);
    box-shadow: rgba(0, 0, 0, 0.15) 0px 8px 24px;
  }
  
  .product-image {
    width: 100%;
    height: auto;
    max-height: 150px;
    object-fit: cover;
    border-radius: var(--border-radius);
    margin-bottom: 8px;
  }
  
  .product-title {
    font-size: 1em;
    color: var(--primary-color);
    font-weight: 500;
    margin: 0;
  }
  
  /* "See More" link */
  .see-more-link {
    text-align: center;
    color: var(--primary-color);
    font-weight: bold;
    cursor: pointer;
    margin-top: 10px;
    transition: color 0.3s;
  }
  
  .see-more-link:hover {
    color: #00465d;
  }
  
  /* Responsive styling */
  @media (max-width: 768px) {
    .category-row {
      flex-direction: column;
    }
  
    .category-container {
      width: 100%;
    }
  }
  
  @media (max-width: 480px) {
    .product-grid {
      grid-template-columns: 1fr;
    }
  }
  